<template>
  <div class="bank">
    <j-header title="我的银行卡" icon="add" iconRouter="addBank"></j-header>
    <main>
      <div v-show="tableData.length > 0" class="list" v-for="obj in tableData" :key='obj.id'>
        <p>
          <svg-icon :icon-class="obj.bank_code ? obj.bank_code: ''"/>
          <span>{{ obj.bank_name }}</span>
          <svg-icon v-show="deleteShow" @click="deleteHandle(obj.id)" icon-class="delete" class="delete wave" />
        </p>
        <span>{{ obj.bank_code }}</span>
        <ul>
          <li>****</li>
          <li>****</li>
          <li>****</li>
          <li>****</li>
          <li>{{ obj.bank_card ? obj.bank_card.slice(-4) : '' }}</li>
        </ul>
      </div>
      <j-nothing
      :show="tableData.length < 1"
      :url="require('@/assets/bank/nothing.png')"
      title="您还没有添加银行卡~"
      routerTitle="立即绑定银行卡"
      router="addBank"></j-nothing>
    </main>
    <footer v-waves @click="setting">
      <svg-icon :icon-class="setStatus"></svg-icon>
      <p>管理银行卡</p>
    </footer>
  </div>
</template>

<script>
// @ is an alias to /src
import JHeader from '@/components/JHeader'
import JNothing from '@/components/JNothing'

export default {
  name: 'bank',
  components: {
    JHeader,
    JNothing
  },
  data () {
    return {
      tableData: [],
      setStatus: 'setting',
      deleteShow: false
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      this.request.get('/bank', { params: { type: 10 } }).then(res => {
        res.code === 1 && (this.tableData = res.data)
      })
    },
    setting () {
      this.setStatus = this.setStatus === 'setting' ? 'setThe' : 'setting'
      this.deleteShow = !this.deleteShow
    },
    deleteHandle (id) {
      this.$confirm('您确定要删除这张银行卡吗？').then(() => {
        this.request({
          url: `/bank/${id}`,
          method: 'delete'
        }).then(res => {
          this.$toast(res.message)
          res.code === 1 && this.init()
        })
      }).catch(() => {
        this.$toast('您已取消操作！')
      })
    }
  },
  mounted () {
  }
}
</script>

<style lang="scss" scoped>
  @import './index.scss';
</style>
